@import "./src/scss/vars";

.button {
    padding: 10px;
    border: 2px solid $accent;
    border-radius: 10px;
    color: $accent;
    background-color: rgba($white, 0.1);
    transition: color 0.5s, background-color 0.5s;
    cursor: pointer;

    &__100 {
        width: 100%;
    }

    &:hover {
        color: $white;
        background-color: $accent;
    }
}